@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap");

.App-header {
  font-size: 1rem;
  text-align: center;
  font-family: "Oswald", sans-serif;
  padding-bottom: 2rem;
}

.carousel-images {
  position: relative;
  height: 82vh;
  max-width: 1350px;
  margin: auto;
  overflow: hidden;
}

.carousel-images img {
  width: 100%;
  height: 99%;
}

.slide_direction {
  display: flex;
  justify-content: space-between;
}

.left,
.right {
  background-color: #fb666675;
  color: #fff;
  padding: 13px 8px 8px 13px;
  /* margin: 20px 20px; */
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto 10px;
  height: 25px;
  width: 25px;
}

.left {
  left: 0;
}

.right {
  right: 0;
}

.carousel-indicator {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.dot {
  background-color: #333;
  width: 10px;
  height: 10px;
  border-radius: 30%;
}

.active {
  background-color: #61dafb;
}

/* Start Sign Up form CSS*/

.signUpContainer {
  background: #23242a;
  justify-content: center;
  display: flex;
  align-items: center;
  min-height: 110vh;
}

.box {
  background: #1c1c1c;
  width: 380px;
  height: 500px;
  position: relative;
  border-radius: 8px;
  overflow: hidden;
}

.box::before {
  background: linear-gradient(0deg, transparent, #45f3ff, #45f3ff);
  position: absolute;
  animation: animate 6s linear infinite;
  content: '';
  top: -100%;
  left: -30%;
  width: 890px;
  height: 730px;
  transform-origin: bottom right;
}

.box::after {
  background: linear-gradient(0deg, transparent, #45f3ff, #45f3ff);
  position: absolute;
  animation: animate 6s linear infinite;
  content: '';
  top: -100%;
  left: -30%;
  width: 890px;
  height: 730px;
  transform-origin: bottom right;
  animation-delay: -3s;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.form {
  inset: 2px;
  z-index: 10;
  position: absolute;
  background: #28292d;
  border-radius: 8px;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
}

.form h2 {
  color: #45f3ff;
  font-weight: 500;
  text-align: center;
  letter-spacing: 0.1em;
}

.inputBox {
  position: relative;
  width: 300px;
  margin-top: 350px;
}

.inputBox input {
  position: relative;
  width: 100%;
  padding: 30px 10px 10px;
  background: transparent;
  border: none;
  outline: none;
  color: #23242a;
  font-size: 1em;
  letter-spacing: 0.05em;
  z-index: 10;
}

.inputBox span {
  position: absolute;
  left: 0;
  bottom: 0px;
  padding: 0px 10px 10px 0px;
  font-size: 1em;
  color: #8f8f8f;
  pointer-events: none;
  letter-spacing: 0.05em;
  transition: 0.05s;
}

.inputBox input:valid~.label,
.inputBox input:focus~.label {
  color: #45f3ff;
  transform: translateX(0px) translateY(-35px);
  font-size: 0.75em;
}

 .inputBox .error {
  color: bisque;
  font-size: 0.75em;
  transform: translateX(0px) translateY(30px);
}

.inputBox i {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #45f3ff;
  border-radius: 4px;
  transition: 0.5s;
  pointer-events: none;
  z-index: 9;
}

.inputBox input:valid~i,
.inputBox input:focus~i {
  height: 44px;
}

.links {
  display: flex;
  justify-content: space-between;
}


.links a:hover,
.links a:nth-child(2) {
  color: #45f3ff;
}

.inputBoxForImage {
